.body1,.about_us .desc{font-size:1rem;font-weight:400;line-height:1.75rem}.body2{font-size:1rem;font-weight:500;line-height:1.75rem}.label1,.about_us .pre_title{font-size:1.125rem;font-weight:500;line-height:2rem}.label2{font-size:1rem;font-weight:500;line-height:1.75rem}.btn{padding:1em 3em;cursor:pointer;white-space:nowrap;text-decoration:none;border:unset;border-radius:2.625em;transition:background-color .3s ease-in-out}.btn.yellow{background-color:#fcd980;color:#1b1c2b}.btn.yellow:hover{background-color:#fde4a6}.btn.dark_blue{color:#fff;background-color:#282938}.btn.dark_blue:hover{background-color:#535460}.arrow_link::after{position:absolute;margin-left:.5em;content:"→";transition:transform .3s ease-in-out}.arrow_link:hover::after{transform:translateX(0.5em)}.about_us{display:flex;align-items:center;justify-content:space-between;padding:6rem 10rem;color:#282938}.about_us h1{margin-top:.2em}.about_us .desc{margin-top:2em;opacity:.7}.about_us img{width:34em;height:21em;margin-left:8em;object-fit:cover}