.body1,.our_team .card_wrapper .card p{font-size:1rem;font-weight:400;line-height:1.75rem}.body2{font-size:1rem;font-weight:500;line-height:1.75rem}.label1{font-size:1.125rem;font-weight:500;line-height:2rem}.label2{font-size:1rem;font-weight:500;line-height:1.75rem}.btn{padding:1em 3em;cursor:pointer;white-space:nowrap;text-decoration:none;border:unset;border-radius:2.625em;transition:background-color .3s ease-in-out}.btn.yellow{background-color:#fcd980;color:#1b1c2b}.btn.yellow:hover{background-color:#fde4a6}.btn.dark_blue{color:#fff;background-color:#282938}.btn.dark_blue:hover{background-color:#535460}.arrow_link::after{position:absolute;margin-left:.5em;content:"→";transition:transform .3s ease-in-out}.arrow_link:hover::after{transform:translateX(0.5em)}.our_team{padding:6rem 10rem;text-align:center;color:#282938;background-color:#f4f6fc}.our_team .card_wrapper{display:flex;justify-content:space-between;margin-top:3em;gap:2em}.our_team .card_wrapper .card{display:flex;flex-direction:column;justify-content:center;width:100%;padding:2em 3em;background-color:#fff}.our_team .card_wrapper .card .image_wrapper{position:relative;overflow:hidden;width:10em;height:10em;margin:0 auto;border-radius:100%}.our_team .card_wrapper .card .image_wrapper img{width:100%;height:100%;object-fit:cover}.our_team .card_wrapper .card .image_wrapper .image_overlay{position:absolute;top:0;width:100%;height:100%;transition:opacity .3s ease-out;opacity:0;border-radius:100%;background:linear-gradient(348.37deg, #5239fa 17.97%, rgba(255, 255, 255, 0) 79.2%)}.our_team .card_wrapper .card .image_wrapper .image_overlay .socials{padding-top:100%;transition:padding-top .3s ease-in-out;pointer-events:none;color:#fff}.our_team .card_wrapper .card .image_wrapper .image_overlay .socials a{pointer-events:auto}.our_team .card_wrapper .card .image_wrapper .image_overlay .socials a svg{font-size:1rem;margin-left:.5em}.our_team .card_wrapper .card .image_wrapper:hover .image_overlay{opacity:1}.our_team .card_wrapper .card .image_wrapper:hover .socials{padding-top:7.5em}.our_team .card_wrapper .card h6{margin-top:1em}.our_team .card_wrapper .card p{opacity:.7}