.body1,.our_process .process_wrapper .process p{font-size:1rem;font-weight:400;line-height:1.75rem}.body2{font-size:1rem;font-weight:500;line-height:1.75rem}.label1{font-size:1.125rem;font-weight:500;line-height:2rem}.label2{font-size:1rem;font-weight:500;line-height:1.75rem}.btn{padding:1em 3em;cursor:pointer;white-space:nowrap;text-decoration:none;border:unset;border-radius:2.625em;transition:background-color .3s ease-in-out}.btn.yellow{background-color:#fcd980;color:#1b1c2b}.btn.yellow:hover{background-color:#fde4a6}.btn.dark_blue{color:#fff;background-color:#282938}.btn.dark_blue:hover{background-color:#535460}.arrow_link::after{position:absolute;margin-left:.5em;content:"→";transition:transform .3s ease-in-out}.arrow_link:hover::after{transform:translateX(0.5em)}.our_process{padding:0 10rem 6rem;color:#282938}.our_process h2{text-align:center}.our_process .process_wrapper{display:flex;align-items:center;justify-content:space-between;margin-top:3em;gap:3.5em}.our_process .process_wrapper .work_line{display:flex;align-items:center}.our_process .process_wrapper .work_line .circle{min-width:1.5em;min-height:1.5em;border-radius:100%;background-color:#2405f2}.our_process .process_wrapper .work_line .line{width:100%;height:.2em;margin-left:1.2em;opacity:.4;background-image:linear-gradient(to right, #2405f2 33%, white 0%);background-position:bottom;background-size:.8em 1px}.our_process .process_wrapper .process h6{margin-top:1.5em}.our_process .process_wrapper .process p{margin-top:.5em;opacity:.7}