.body1,.who_we_are .top .content_wrapper article p{font-size:1rem;font-weight:400;line-height:1.75rem}.body2{font-size:1rem;font-weight:500;line-height:1.75rem}.label1{font-size:1.125rem;font-weight:500;line-height:2rem}.label2,.who_we_are .top>p{font-size:1rem;font-weight:500;line-height:1.75rem}.btn{padding:1em 3em;cursor:pointer;white-space:nowrap;text-decoration:none;border:unset;border-radius:2.625em;transition:background-color .3s ease-in-out}.btn.yellow{background-color:#fcd980;color:#1b1c2b}.btn.yellow:hover{background-color:#fde4a6}.btn.dark_blue{color:#fff;background-color:#282938}.btn.dark_blue:hover{background-color:#535460}.arrow_link::after{position:absolute;margin-left:.5em;content:"→";transition:transform .3s ease-in-out}.arrow_link:hover::after{transform:translateX(0.5em)}.who_we_are{box-sizing:border-box;width:100%;padding:0 10rem 6rem;color:#282938}.who_we_are .top{padding:5em;background-color:#f4f5f5}.who_we_are .top .content_wrapper{display:flex;align-items:center;justify-content:space-between}.who_we_are .top .content_wrapper article{width:48%}.who_we_are .top .content_wrapper article p{margin-top:1em;opacity:.7}.who_we_are>img{width:100%;height:21.25em;object-fit:cover}